.btn
{
  font-size: 14px;
  font-weight: bold;

  padding: 5px 23px;

  transition: all .3s;

  border: 2px solid $btn-border-color;
  border-radius: 4px;
  background: transparent;
  box-shadow: 0 1px 2px rgba($btn-box-shadow-color,.1);

  &.btn-sm
  {
    font-size: 12px;
    padding: 4px 23px;
  }
}
